.sus-container {
    background: url('/public/image/foodbackground.jpg') center center/cover no-repeat; 
    height: 370vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: top;
    align-items: center; 
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain; 
}

.photo-text {
    position: absolute;
    top: 80px; /* Adjust as needed */
    right: 10px; /* Adjust as needed */
    background-color: rgba(52, 44, 44, 0.5); /* Semi-transparent black background */
    color: white;
    padding: 5px 10px; /* Adjust padding as needed */
    font-size: 14px; /* Adjust font size as needed */
  }

.sus-container > h1 {
    color: #084634;
    background-color: rgb(255, 255, 255);
    font-size: 50px;
    padding: 0px 30px;
    margin-top: 50px;
    margin-bottom: 30px;
    margin-left: 30px;
    max-width: 60%;
    text-align: center;
}

.sus-container > p {
  font-family: 'Trebuchet MS', 'Helvetica', 'Lucida Grande',
  'Lucida Sans', Arial, sans-serif;
  background-color: rgba(255, 255, 255, 0.863); 
  text-align: justify;
  margin-left: 30px;
  margin-bottom: 30px;
  padding: 10px 20px;
  color: #084634;
  font-size: 20px;
  max-width: 50%; /* Set maximum width to 50% of the parent container */
}

.sus-container > h2 {
    color: #084634;
    background-color: rgba(255, 255, 255, 0.945);
    padding: 0px 40px;
    margin-bottom: 30px;
    font-size: 30px;
    margin-top: 30px;
    margin-left: 35px;
  }
  